.App {
  text-align: center;
}

#words{
  width: 80%;
  margin-left: 10%;
  text-align: left;
  padding: 10px;
  font-size: 18px;
  border-collapse: collapse;
}

#words tr:nth-child(odd){
  background-color: rgb(194, 173, 183);
  width: 300px;
}

#words th{
  background-color: rgb(107, 53, 124);
  color: white;
  padding: 15px;
  letter-spacing: 2px;
}

#words td, #words th {
  border: 1px solid #ddd;
  padding: 8px;
}

.main{
  height: 80vh;
  overflow: auto;
}

button{
  width: 150px;
  height: 30px;
  border: 0;
  background-color: rgb(81, 35, 99);
  color: white;
  margin-top: 40px;
  margin-bottom: 20px;
}
input{
  margin-top: 40px;
  margin-bottom: 20px;
  width: 150px;
  height: 25px;
  border: 1px solid black;
}